import reflections from "./reflections.js"
import ReflectSemester from "../components/ReflectSemester";
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';

/*
Displays the total course list + blurbs, grouped by year and semester.
*/

export default function PageReflectList() {
    const allyears = reflections.map(entry => entry.year)

    return (
    <>
        <VerticalTimeline
            animate={false}
            layout="1-column-right"
            lineColor="black"
        >
            <VerticalTimelineElement
            date={2100}
            >
                <p>1232342342342342423</p>
            </VerticalTimelineElement>
            <VerticalTimelineElement
            date={2101}
            >
                <p>209rr932</p>
            </VerticalTimelineElement>
            <VerticalTimelineElement
            date={2110}
            >
                <p>woifjofi23jf</p>
            </VerticalTimelineElement>
        </VerticalTimeline>
    </>
    );
}

